import { CloudSync as AdbIcon, Menu as MenuIcon } from "@mui/icons-material";
import {
  Alert,
  AppBar,
  Avatar,
  Box,
  Button,
  Card,
  CardContent,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from "@mui/material";
import { MouseEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import "./App.css";
import { actions, storeSelector } from "./config/redux";
import { Get, setToken } from "./config/server";
import { t, tField } from "./utils";
import { Login } from "./views";

function App() {
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { accessToken } = storeSelector(({ auth }) => auth);
  const { alert } = storeSelector(({ settings }) => settings);
  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);
  const [title, setTitle] = useState<string>("");
  const pages = [
    {
      title: t(tField.titlePrices),
      path: "/prices",
    },
    {
      title: t(tField.titleGoods),
      path: "/goods",
    },
    {
      title: t(tField.titleSettings),
      path: "/settings",
    },
  ];

  const handleOpenNavMenu = (event: MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = () => {
    navigate("/settings");
  };

  const handleCloseNavMenu = (path: string) => {
    setAnchorElNav(null);
    path && navigate(path);
  };

  useEffect(() => {
    const page = pages.find((item) => item.path === location.pathname);
    setTitle(page?.title || "");
  }, [location.pathname]);

  useEffect(() => {
    if (accessToken) {
      setToken(accessToken);
      Get(["optionsByToken", accessToken], (res: ServerRes.Options) => {
        const { options, currencyObj, currencyArr } = res;
        dispatch(actions.options.update({ currencyObj, currencyArr, ...options }));
      });
    }
  }, [accessToken]);

  return (
    <Container sx={{ paddingTop: "5px" }}>
      <AppBar position="static" sx={{ marginBottom: "5px" }}>
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} />
            <Typography
              variant="h6"
              noWrap
              component="a"
              href="/"
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}>
              {t(tField.logo)}
            </Typography>

            <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit">
                <MenuIcon />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block", md: "none" },
                }}>
                {pages.map((page, index) => (
                  <MenuItem key={index} onClick={() => handleCloseNavMenu(page.path)}>
                    <Typography textAlign="center">{page.title}</Typography>
                  </MenuItem>
                ))}
              </Menu>
            </Box>
            <AdbIcon sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />
            <Typography
              variant="h5"
              noWrap
              component="a"
              href="/"
              sx={{
                mr: 2,
                display: { xs: "flex", md: "none" },
                flexGrow: 1,
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}>
              {t(tField.logo)}
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
              {pages.map((page, index) => (
                <Button key={index} onClick={() => navigate(page.path)} sx={{ my: 2, color: "white", display: "block" }}>
                  {page.title}
                </Button>
              ))}
            </Box>
            <Box sx={{ flexGrow: 0 }}>
              <Tooltip title={t(tField.openSettings)}>
                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                  <Avatar alt="Test" src="" />
                </IconButton>
              </Tooltip>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Card sx={{ minHeight: "600px", marginBottom: "5px", "& .MuiAlert-root": { my: 1 } }}>
        <CardContent>
          <Typography sx={{ marginBottom: "1em" }} variant="h5" component="div">
            {title}
          </Typography>
          {alert.show && <Alert severity={alert.type}>{alert.msg}</Alert>}
          {accessToken ? <Outlet /> : <Login />}
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <Typography variant="body2" color="text.secondary">
            {t(tField.support)}
            <Typography variant="body2" color="text.secondary" component="a" href="tel:380979044874">
              +38 097 90 44 874
            </Typography>
          </Typography>
          <Typography variant="body2" color="text.secondary" align="center">
            {t("footer")}
          </Typography>
        </CardContent>
      </Card>
    </Container>
  );
}

export default App;
