import LoadingButton from "@mui/lab/LoadingButton";
import { Box, Modal, TextField } from "@mui/material";
import { DataGrid, GridRowId } from "@mui/x-data-grid";
import { useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { BACKEND_URL } from "../common/constants";
import { TableCardColumns } from "../common/tables";
import { actions, storeSelector } from "../config/redux";
import { Get, OpenPost, OpenPostAsync, Post, PostAsync } from "../config/server";
import { t, tField } from "../utils";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 800,
  bgcolor: "background.paper",
  borderRadius: 2,
  boxShadow: 24,
  p: 4,
  "& .MuiTextField-root": { my: 1, width: "25ch" },
  "& .MuiButton-root": { my: 1 },
};

function Goods() {
  const { promToken, fieldNameForPromId, groupIdForNewProducts, storeId, firmId, currencyObj, fieldNameForMinWholeSaleQty, currencyArr } =
    storeSelector(({ options }) => options);
  const dispatch = useDispatch();
  const [promUaLoading, setPromUaLoading] = useState(false);
  const [ukrSkladLoading, setUkrSkladLoading] = useState(false);
  const [downloadPropuctsLoading, setDownloadPropuctsLoading] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [groups, setGroups] = useState<Group[]>([]);
  const [filteredGroups, setFilteredGroups] = useState<Group[]>([]);
  //const [selection, setSelection] = useState<GridRowId[]>([]);
  const [modal, setModal] = useState({
    onClick: () => {},
    btnName: "",
  });
  const selection = useRef<GridRowId[]>([]);

  const fromPromUaToUkrSklad = () => {
    // clear alert
    dispatch(actions.settings.setAlert({ show: false, msg: "", type: "success" }));
    setPromUaLoading(true);
    Post(
      "promProducts",
      { token: promToken },
      (products: Product[]) => {
        setAlert({
          msg: t(tField.productsDownloaded),
          type: "info",
        });
        Get(
          "promGroups",
          (groups: Group[]) => {
            setAlert({
              msg: t(tField.groupsDownloaded),
              type: "info",
            });
            const promIds = products.map((item) => item.id);
            OpenPost(
              `${BACKEND_URL}product/compare`,
              { products: promIds, fieldNameForPromId, storeId, firmId },
              (productsWithId: ProductCompare[]) => {
                if (products.length === productsWithId.length) {
                  dispatch(
                    actions.settings.setAlert({
                      show: true,
                      msg: t(tField.infoAllProductsFound),
                      type: "success",
                    })
                  );
                  return console.log(" all products found: ");
                }
                // find and add groups
                OpenPost(
                  `${BACKEND_URL}group/patch`,
                  { groups, groupIdForNewProducts: groupIdForNewProducts, storeId },
                  (groups: GroupUkr[]) => {
                    // find and add goods
                    const productsWithoutId = products.filter(
                      (item) => !productsWithId.find((item2) => +item2[fieldNameForPromId] === item.id)
                    );
                    // search by name in ukrsklad if not found -> add new product
                    OpenPost(
                      `${BACKEND_URL}product/patch`,
                      {
                        products: productsWithoutId,
                        fieldNameForPromId,
                        groupIdForNewProducts,
                        groups,
                        currencyObj,
                        storeId,
                        firmId,
                      },
                      (products: Product[]) => {
                        setPromUaLoading(false);
                        dispatch(
                          actions.settings.setAlert({
                            show: true,
                            msg: `${t(tField.infoProductsSync)}: ${products.length}`,
                            type: "success",
                          })
                        );
                      },
                      (err) => {
                        setPromUaLoading(false);
                        setAlertError(err);
                      }
                    );
                  },
                  (err) => {
                    setPromUaLoading(false);
                    setAlertError(err);
                  }
                );
              },
              (err) => {
                setPromUaLoading(false);
                setAlertError(err?.message || err);
              }
            );
          },
          (err) => {
            setPromUaLoading(false);
            setAlertError(err);
          }
        );
      },
      (err) => {
        setPromUaLoading(false);
        setAlertError(err);
      }
    );
  };

  const fromUkrSkladToPromUa = async () => {
    //clear alert
    dispatch(actions.settings.setAlert({ show: false, msg: "", type: "success" }));
    setUkrSkladLoading(true);
    try {
      // get all products from prom.ua
      const promProducts = await PostAsync<Product[]>("promProducts", { token: promToken });
      if (!promProducts) {
        setAlertError(t(tField.errorNoProductsFromPromUa));
        return;
      }
      // factory we need {id, presence, }
      const promProductsForXlsx = promProducts.map((item) => {
        return {
          id: item.id,
          presence: item.presence,
          group: item.group,
        };
      });

      // request to server for create .xlsx file
      const res = await OpenPostAsync(`${BACKEND_URL}file/xlsx-to-prom`, {
        fieldNameForPromId,
        fieldNameForMinWholeSaleQty,
        promProducts: promProductsForXlsx,
        currencyArr,
        storeId,
        firmId,
      });
      if (res) setAlert({ msg: t(tField.infoFileCreated), type: "success" });
    } catch (error) {
      setAlertError(`${error}`);
    }
    setUkrSkladLoading(false);
  };

  const setAlertError = (err: string) => {
    dispatch(actions.settings.setAlertError(err));
  };

  const setAlert = (data: { msg: string; type: string }) => {
    dispatch(actions.settings.setAlert(data));
  };

  const downloadGroups = () => {
    //clear alert
    dispatch(actions.settings.setAlert({ show: false, msg: "", type: "success" }));
    setOpenModal(true);
    Get("promGroups", (groups: Group[]) => {
      //find parent group name and add to property
      const groupsWithParentName = groups.map((item) => {
        const parent = groups.find((item2) => item2.id === item.parent_group_id);
        return {
          ...item,
          parent_name: parent?.name || "",
        };
      });
      setGroups(groupsWithParentName);
      setFilteredGroups(groupsWithParentName);
    });
  };

  const downloadProducts = () => {
    //clear alert
    dispatch(actions.settings.setAlert({ show: false, msg: "", type: "success" }));
    setDownloadPropuctsLoading(true);
    if (!selection.current.length) {
      setDownloadPropuctsLoading(false);
      return console.log(" selection is empty: ");
    }
    Post("promProductsByGroups", { token: promToken, groupIds: selection.current }, (products: Product[]) => {
      const promIds = products.map((item) => item.id);
      OpenPost(
        `${BACKEND_URL}product/compare`,
        { products: promIds, fieldNameForPromId, storeId, firmId },
        (productsWithId: ProductCompare[]) => {
          if (products.length === productsWithId.length) {
            setOpenModal(false);
            dispatch(
              actions.settings.setAlert({
                show: true,
                msg: t(tField.infoAllProductsFound),
                type: "success",
              })
            );
            return console.log(" all products found: ");
          }
          // find and add groups
          OpenPost(`${BACKEND_URL}group/patch`, { groups, groupIdForNewProducts: groupIdForNewProducts, storeId }, (groups: GroupUkr[]) => {
            const productsWithoutId = products.filter((item) => !productsWithId.find((item2) => +item2[fieldNameForPromId] === item.id));
            OpenPost(
              `${BACKEND_URL}product/patch`,
              {
                products: productsWithoutId,
                fieldNameForPromId,
                groupIdForNewProducts,
                groups,
                currencyObj,
                storeId,
                firmId,
              },
              (products: Product[]) => {
                setOpenModal(false);
                setDownloadPropuctsLoading(false);
                dispatch(
                  actions.settings.setAlert({
                    show: true,
                    msg: `${t(tField.infoProductsSync)}: ${products.length}`,
                    type: "success",
                  })
                );
              }
            );
          });
        }
      );
    });
  };

  const fromUkrSkladToPromUaByGroups = async () => {
    //clear alert
    dispatch(actions.settings.setAlert({ show: false, msg: "", type: "success" }));
    setUkrSkladLoading(true);
    if (!selection.current.length) {
      setUkrSkladLoading(false);
      return console.log(" selection is empty: ");
    }
    try {
      // get all products from prom.ua
      const promProducts = await PostAsync<Product[]>("promProductsByGroups", { token: promToken, groupIds: selection.current });
      if (!promProducts) {
        setAlertError(t(tField.errorNoProductsFromPromUa));
        return;
      }
      // factory we need {id, presence, }
      const promProductsForXlsx = promProducts.map((item) => {
        return {
          id: item.id,
          presence: item.presence,
          group: item.group,
        };
      });

      // request to server for create .xlsx file
      const res = await OpenPostAsync(`${BACKEND_URL}file/xlsx-to-prom`, {
        fieldNameForPromId,
        fieldNameForMinWholeSaleQty,
        promProducts: promProductsForXlsx,
        currencyArr,
      });
      if (res) setAlert({ msg: t(tField.infoFileCreated), type: "success" });
    } catch (error) {
      setAlertError(`${error}`);
    }
    setOpenModal(false);
    setUkrSkladLoading(false);
  };

  const promToUkrsklad = () => {
    setModal({
      btnName: t(tField.btnDownloadProducts),
      onClick: downloadProducts,
    });
    downloadGroups();
  };

  const ukrskladToProm = () => {
    setModal({
      btnName: t(tField.btnCreateXlsxFile),
      onClick: fromUkrSkladToPromUaByGroups,
    });
    downloadGroups();
  };

  const handleCloseModal = () => {
    selection.current = [];
    setOpenModal(false);
  };

  const handelFilterGroup = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    const filteredGroups = groups.filter((item) => item.name.toLowerCase().includes(value.toLowerCase()));
    setFilteredGroups(filteredGroups);
  };

  const handleSelectionChange = (row: GridRowId[]) => {
    selection.current = row;
  };

  return (
    <Box sx={{ minWidth: 120, "& > button": { m: 1 }, display: "flex", flexDirection: "column" }}>
      <LoadingButton loading={promUaLoading} onClick={fromPromUaToUkrSklad} variant="outlined" size="medium" type="button">
        {t(tField.prodPromUaToUkrSklad)}
      </LoadingButton>
      <LoadingButton loading={ukrSkladLoading} onClick={fromUkrSkladToPromUa} variant="outlined" size="medium" type="button">
        {t(tField.prodUkrSkladToPromUa)}
      </LoadingButton>
      <LoadingButton onClick={promToUkrsklad} variant="outlined" size="medium" type="button">
        {t(tField.prodPromUaToUkrSkladByGroup)}
      </LoadingButton>
      <LoadingButton onClick={ukrskladToProm} variant="outlined" size="medium" type="button">
        {t(tField.prodUkrSkladToPromUaByGroup)}
      </LoadingButton>
      <Modal open={openModal} onClose={handleCloseModal} aria-labelledby="modal-modal-title" aria-describedby="modal-modal-description">
        <Box sx={style}>
          <TextField
            id="filter-group"
            label={t(tField.inputFilterGroup)}
            name="filterGroups"
            onChange={handelFilterGroup}
            size="small"
            type="text"
          />
          <div style={{ height: 800, width: "100%" }}>
            <DataGrid
              columns={TableCardColumns}
              rows={filteredGroups}
              checkboxSelection
              getRowId={(row: any) => row.id}
              onSelectionModelChange={handleSelectionChange}
              pageSize={10}
              rowsPerPageOptions={[10, 20, 50]}
            />
          </div>
          <LoadingButton loading={downloadPropuctsLoading} onClick={modal.onClick} variant="outlined" size="medium" type="button">
            {modal.btnName}
          </LoadingButton>
        </Box>
      </Modal>
    </Box>
  );
}

export default Goods;
